<template>
  <div style="display: flex">
    <div class="intro-text"
         style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router
               background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/taskCreate">
          <i class="el-icon-caret-right"></i>
          <span slot="title">创建</span>
        </el-menu-item>
        <el-menu-item index="/taskView">
          <i class="el-icon-caret-right"></i>
          <span slot="title">查看</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <!--    主体区域-->
    <div style="padding: 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="danger" plain @click="delBatch">批量删除</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
      </div>
      <el-table :data="tableData" height="530" border stripe
                :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}"
                @selection-change="handleSelectionChange" style="max-width:calc(100vw - 155px - 150px - 40px)">
        <el-table-column type="selection" align="center" width="50"></el-table-column>
        <!--        <el-table-column label="序号" prop="id" align="center"></el-table-column>-->
        <el-table-column label="任务包名称" prop="packageName" align="center" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="任务类型" prop="type" align="center" width="110"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="任务描述" prop="description" align="center"
                         :show-overflow-tooltip="true"></el-table-column>
<!--        <el-table-column label="任务周期" prop="taskCycle" align="center" width="100"-->
<!--                         :show-overflow-tooltip="true"></el-table-column>-->
        <el-table-column label="点检周期" prop="checkCycle" align="center" width="70"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="超时时间" prop="timeout" align="center" width="100"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="点检设备/仪表" align="center" width="120">
          <template v-slot="scope">
            <el-tooltip class="item" effect="dark" :content="scope.row.equipments || scope.row.meters" placement="top">
              <div class="custom-tooltip">
                <span v-if="scope.row.equipments">{{ scope.row.equipments }}</span>
                <span v-else>{{ scope.row.meters }}</span>
              </div>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="点检人员" prop="checkPeople" align="center" width="120"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="开始时间" prop="startTime" align="center" width="130"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="结束时间" prop="endTime" align="center" width="130"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="状态" prop="state" align="center" width="50"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="领取人员" prop="receivePeople" align="center" width="80"
                         :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="195" fixed="right">
          <template v-slot="scope">
            <div style="display: flex; justify-content: flex-end; align-items: center; width: 100%;">
              <el-switch v-if="scope.row.type==='设备点检任务'||scope.row.type==='仪表点检任务'" v-model="scope.row.isPublish"
                         @change="changeOpen(scope.row)" active-color="#13ce66" inactive-color="#ff4949"
                         style="margin-right: 10px"></el-switch>

              <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" plain @click="del(scope.row.id)">删除</el-button>
            </div>
          </template>

        </el-table-column>
      </el-table>
      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
      <el-dialog title="修改任务包信息" style="align-items: center" :visible.sync="fromVisible" width="40%">
        <el-form :model="form" label-width="120px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="任务包名称" prop="packageName">
            <el-input v-model="form.packageName" placeholder="任务包名称"></el-input>
          </el-form-item>
          <el-form-item label="任务类型" prop="type">
            <el-input v-model="form.type" placeholder="任务类型"></el-input>
          </el-form-item>
          <el-form-item label="任务描述" prop="description">
            <el-input type="textarea" v-model="form.description" placeholder="任务描述"></el-input>
          </el-form-item>
<!--          <el-form-item label="任务周期" prop="taskCycle">-->
<!--            <el-input v-model="form.taskCycle" placeholder="任务周期"></el-input>-->
<!--          </el-form-item>-->
          <el-form-item label="点检周期" prop="checkCycle">
            <el-input v-model="form.checkCycle" placeholder="点检周期"></el-input>
          </el-form-item>
          <!--          <el-form-item label="点检设备" prop="equipments"><el-input v-model="form.equipments" placeholder="点检设备"></el-input></el-form-item>-->
          <!--          <el-form-item label="点检人员" prop="people"><el-input v-model="form.checkPeople" placeholder="点检人员"></el-input></el-form-item>-->
          <div style="display: flex;">
            <el-form-item label="开始时间" prop="startTime">
              <el-input v-model="form.startTime" placeholder="开始时间"></el-input>
            </el-form-item>
            <el-form-item label="结束时间" prop="endTime">
              <el-input v-model="form.endTime" placeholder="结束时间"></el-input>
            </el-form-item>
          </div>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>

    </div>

  </div>


</template>

<script>
import {formatTime} from "@/utils";

export default {
  name: "TaskView",
  data() {
    return {
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      packageName: '',
      equipments: '',
      type: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        account: [
          {required: true, message: '请输入账号', trigger: 'blur'},
        ]
      },
      ids: []
    }
  },
  created() {
    this.load()
    console.log(this.user)
  },
  methods: {
    changeOpen(form) {
      console.log(form)
      // 调用更新的接口  更新数据到数据库
      this.$request.put('/task/updateIsPublish', form.id
      ).then(res => {
        if (res.code === '200') {
          this.$message.success('更新成功')
        } else {
          this.$message.error(res.msg)
        }
        this.load(this.pageNum);
      });
      // if(this.form.taskCycle==='周期任务'){
      //   let data = new Date();
      //   data.setMinutes(data.getMinutes() + 2);
      //   this.form.createTime = formatTime(data,'yyyy-MM-ddTHH:mm');
      // }
      // this.sendSaveRequest()   // 直接发请求就可以了
      // console.log('请求')
    },
    // 截断文本，超出部分显示省略号
    truncateText(text) {
      if (!text) return '';
      const maxLength = 7; // 最大长度
      if (text.length > maxLength) {
        return text.slice(0, maxLength) + '...'; // 超过最大长度时显示省略号
      }
      return text;
    },
    exportData() {   //批量导出数据
      if (!this.ids.length) {  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/taskView/export?token=' + this.user.token)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/taskView/export?token=' + this.user.token + '&ids=' + idStr)
      }

    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/task/delete/batch', {data: this.ids}).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {
      })
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/task/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {
      })
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.sendSaveRequest()
        }
      })
    },
    sendSaveRequest() {
      this.$request({
        url: this.form.id ? '/task/update' : '/task/add',
        method: this.form.id ? 'PUT' : 'POST',
        data: this.form
      }).then(res => {
        if (res.code === '200') {  // 表示成功保存
          this.$message.success('保存成功')
          this.load(1)
          this.fromVisible = false
        } else {
          this.$message.error(res.msg)  // 弹出错误的信息
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },

    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    load(pageNum) {
      console.log('loading')
      if (pageNum) this.pageNum = pageNum
      this.$request.get("/taskView/selectByPage", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          searchText: this.searchText
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        console.log(this.tableData)
      })
    },
  }
}
</script>

<style>
.custom-tooltip {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>